import { Component } from '@angular/core';

import { Platform, IonRouterOutlet } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { PushService } from './providers/PushService/PushService.service';
import { InicioService } from './providers/Almacenamiento/inicio.service';
import { ServicioRedService } from './providers/Red/servicio-red.service';
import { Socket } from 'ngx-socket-io';
import { ConexionService } from './providers/Conexiones/Conexion.service';
import { EntradasServices } from './providers/cargas/Entradas.service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
 
})
export class AppComponent {
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    
    private almacenamiento: InicioService,
    private redService: ServicioRedService,
    private conexion: ConexionService,
    private entradas: EntradasServices,
    private pushService: PushService,
  ) {
    
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then( async () => {
      this.statusBar.backgroundColorByHexString('#273238');
      this.splashScreen.hide();
      
      await this.almacenamiento.crearBaseDeDatosSQLite();
      this.pushService.configuracionInicial();
      await this.conexion.cargaLista();
      this.entradas.crearEntrada();
    });
  }

}
